@font-face {
  font-family: sans-serif;
  src: url(../public/GelderSans-Bold.ttf);
}

  

  
  
@media only screen and (min-width: 480px){
  

  .layout{
    display: grid;
    height: 100vh;
    grid-template-columns: 0.9fr 1.2fr 1.2fr 1.2fr 1.4fr;
    grid-template-rows: 0.1fr 1fr 1fr 1fr;
    grid-template-areas: "content1 content2 content2 content2 content2"
                         "content3 content6 content6 content6 content6"
                         "content4 content6 content6 content6 content6"
                         "content4 content6 content6 content6 content6";
                         
                         
                         
  }

}

